@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes selected {
  from {
    border: 6px solid rgba(196, 30, 58, 0); }
  50% {
    border: 6px solid rgba(196, 30, 58, 0); }
  51% {
    border: 6px solid rgba(196, 30, 58, 0.6); }
  to {
    border: 6px solid rgba(196, 30, 58, 0.6); } }

@keyframes selected {
  from {
    border: 6px solid rgba(196, 30, 58, 0); }
  50% {
    border: 6px solid rgba(196, 30, 58, 0); }
  51% {
    border: 6px solid rgba(196, 30, 58, 0.6); }
  to {
    border: 6px solid rgba(196, 30, 58, 0.6); } }

.App {
  text-align: center;
  font-family: roboto; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.header {
  height: 70px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  padding: 0 30px;
  justify-content: space-between; }
  .header .left {
    display: flex; }
    .header .left div {
      margin: 0 20px; }
    .header .left #cmu {
      font-size: 18px;
      font-weight: bold;
      color: #c41e3a; }
    .header .left .title {
      font-size: 18px;
      font-weight: bold;
      color: rgba(0, 0, 0, 0.5); }
  .header .subtitle {
    margin: 0 20px;
    color: rgba(0, 0, 0, 0.5);
    font-size: 16px; }

.mainContent {
  padding: 60px 5%;
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  justify-content: space-around; }
  .mainContent .robot_wrapper {
    width: 260px;
    max-width: 100%; }
    .mainContent .robot_wrapper img {
      margin: 0;
      padding: 0; }
    .mainContent .robot_wrapper #head {
      width: 50%;
      -webkit-transition: .15s;
      transition: .15s; }
    .mainContent .robot_wrapper #body {
      width: 100%;
      margin-top: -5px; }
  .mainContent .question {
    width: calc(90% - 270px);
    max-width: 512px; }
    .mainContent .question .title {
      font-size: 18px;
      font-weight: bold;
      color: rgba(0, 0, 0, 0.5);
      margin: 16px 0; }
    .mainContent .question .subtitle {
      margin: 16px 0;
      color: rgba(0, 0, 0, 0.9);
      font-size: 16px; }
      .mainContent .question .subtitle.center {
        width: 100%;
        text-align: center;
        font-size: 18px;
        font-weight: bold; }
    .mainContent .question .chart {
      width: 100%;
      text-align: center;
      margin: 30px 0; }
  .mainContent .chartwrap {
    margin: 30px 0; }
    .mainContent .chartwrap .title {
      font-size: 18px;
      font-weight: bold;
      color: rgba(0, 0, 0, 0.9);
      margin-bottom: 30px; }

.samples {
  display: flex;
  margin: 40px 0; }
  .samples img {
    max-width: 100px;
    height: 100px;
    margin: 0 10px;
    border-radius: 12px;
    border: 6px solid rgba(196, 30, 58, 0);
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s; }
    .samples img:hover, .samples img.selected {
      border: 6px solid rgba(196, 30, 58, 0.7); }

.btn {
  display: block;
  padding: 10px 60px;
  margin: 10px 0;
  background: rgba(0, 0, 0, 0.2);
  font-size: 16px;
  font-weight: bold;
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.5);
  -webkit-transition: .3s;
  transition: .3s; }
  .btn.active {
    background: #c41e3a;
    color: #fff;
    cursor: pointer; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

